@import url(https://fonts.googleapis.com/css2?family=Anton&family=Coda+Caption:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sacramento&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{scroll-behavior:smooth;width:100%;height:100%;margin:0px;padding:0px;overflow-x:hidden}.App{text-align:center;color:white;font-family:Arial,Helvetica,sans-serif;background-color:#141114;background-image:linear-gradient(335deg, black 23px, transparent 23px),linear-gradient(155deg, black 23px, transparent 23px),linear-gradient(335deg, black 23px, transparent 23px),linear-gradient(155deg, black 23px, transparent 23px);background-size:58px 58px;background-position:0px 2px, 4px 35px, 29px 31px, 34px 6px}#nav{display:flex;flex-direction:row;justify-content:space-around;align-items:center;position:fixed;top:0%;font-family:"Coda Caption",sans-serif;background-color:black;font-size:1.5em;padding:10px;z-index:20;width:100vw}a{margin:5px;text-decoration:none;-webkit-text-decoration-line:none;text-decoration-line:none;color:#F1E8A2}#greet{position:absolute;top:15vh;z-index:10;color:#fcfafa;font-family:"Coda Caption",sans-serif}#greet-background{object-fit:cover;width:100vw;height:100vh}.fill{height:1.5em}@keyframes shake{0%{transform:translate(1px, 1px) rotate(0deg)}10%{transform:translate(-1px, -2px) rotate(-1deg)}20%{transform:translate(-3px, 0px) rotate(1deg)}30%{transform:translate(3px, 2px) rotate(0deg)}40%{transform:translate(1px, -1px) rotate(1deg)}50%{transform:translate(-1px, 2px) rotate(-1deg)}60%{transform:translate(-3px, 1px) rotate(0deg)}70%{transform:translate(3px, 1px) rotate(-1deg)}80%{transform:translate(-1px, -1px) rotate(1deg)}90%{transform:translate(1px, 2px) rotate(0deg)}100%{transform:translate(1px, -2px) rotate(-1deg)}}#resume-btn{position:fixed;top:85vh;right:10px;border-radius:50%;background-color:transparent;padding:10px}#resume-btn :hover{animation:shake 0.5s}.card{border:2px solid black;background-color:#564f4f;border-radius:5px;width:400px;margin:auto;margin-top:5px;padding:4px;overflow-y:hidden;overflow-x:hidden;height:58vh}.inner-card{background-color:black;padding:4px;margin:5px}.work-title{background-color:#f7e013;color:black;padding:4px;margin:5px}.work-description{margin:5px;padding:4px}.work-image{width:100%;height:100%;object-fit:cover}.work-tools{margin:5px}.work-btn{display:flex;align-items:center;justify-content:center;padding:0.5em;width:100%}.code-btn{position:relative;right:-50vw}.web-btn{position:relative;left:-50vw}.card-btn{border-color:#f1c40f;color:#564F4F;background-image:linear-gradient(45deg, #f1c40f 50%, transparent 50%);background-position:100%;background-size:400%;transition:background 500ms ease-in-out}.card-btn:hover{background-position:0}.btn{box-sizing:border-box;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:transparent;border:2px solid #F7E013;border-radius:0.6em;color:#F7E013;cursor:pointer;display:flex;align-self:center;font-size:2em;font-weight:400;line-height:1;margin:10px;padding:.2em .8em;text-decoration:none;text-align:center}.btn:hover,.btn:focus{color:#564F4F;border:2px solid black;outline:2px}@keyframes web-anim{from{left:-50vw}to{left:0}}@keyframes code-anim{from{right:-50vw}to{right:0}}.card:hover .code-btn{animation:code-anim 0.5s forwards}.card:hover .web-btn{animation:web-anim 0.5s forwards}.card:hover{border:2px solid #f7e013}.category{margin:5em;font-family:"Coda Caption",sans-serif;font-size:1.5em;color:#F1E8A2}.projects-wrapper{display:flex;flex-flow:row wrap;justify-content:space-around}.row{display:flex;flex-direction:row;justify-content:center;align-items:center}.col{display:flex;flex-direction:column;justify-content:center;align-items:center}#contact{width:100vw;height:100vh;justify-content:space-between;align-items:center}.heading+p{font-family:"Coda Caption",sans-serif;font-size:2.5em;position:relative;bottom:15px}#contact-methods{width:60vw;position:relative;top:-2em;justify-content:space-around !important}#contact-methods>a{height:20vh;color:#EFD7F1}#contact-methods>a>p{font-size:2em}@keyframes contact-anim{from{font-size:2em}to{font-size:2.1em}}#contact-methods>a>p :hover{animation:contact-anim 0.5s forwards}footer>p{margin-bottom:2em}@media screen and (max-width: 1000px){#greet{flex-direction:column}.card{margin-top:1.5em}}@media screen and (max-width: 600px){#nav{font-size:1em}.fill{height:4em}.projects-wrapper{display:flex;flex-flow:nowrap;flex-direction:column;justify-content:center;align-items:center;width:100%}.card{margin-top:1.5em;width:80%}.heading{font-size:4em !important}.heading+p{font-size:2em;position:relative;bottom:15px}}@media screen and (max-width: 450px){.card{height:54vh}}

@font-face {
    font-family: Clip;
    src: url("https://acupoftee.github.io/fonts/Clip.ttf");
  }
  
 
  
  .sign {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 50%;
    background-image: radial-gradient(
      ellipse 50% 35% at 50% 50%,
      #31186b,
      transparent
    );
    transform: translate(-50%, -50%);
    letter-spacing: 2;
    left: 50%;
    top: 50%;
    font-family: "Clip";
    text-transform: uppercase;
    font-size: 2em;
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #7512f7,
      -0.2rem 0.1rem 1rem #1b66f1, 0.2rem 0.1rem 1rem #0c2ff0,
      0 -0.5rem 2rem #244cff, 0 0.5rem 3rem #a18dfa;
    animation: shine 2s forwards, flicker 3s infinite;
  }
  
  @keyframes blink {
    0%,
    22%,
    36%,
    75% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #244cff,
        -0.2rem 0.1rem 1rem #244cff, 0.2rem 0.1rem 1rem #244cff,
        0 -0.5rem 2rem #0c2ff0, 0 0.5rem 3rem #0c2ff0;
    }
    28%,
    33% {
      color: #244cff;
      text-shadow: none;
    }
    82%,
    97% {
      color: #0c2ff0;
      text-shadow: none;
    }
  }
  
  .flicker {
    animation: shine 2s forwards, blink 3s 2s infinite;
  }
  
  .fast-flicker {
    animation: shine 2s forwards, blink 10s 1s infinite;
  }
  
  @keyframes shine {
    0% {
      color: #27186b;
      text-shadow: none;
    }
    100% {
      color: #ffe6ff;
      text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #244cff,
        -0.2rem 0.1rem 1rem #244cff, 0.2rem 0.1rem 1rem #244cff,
        0 -0.5rem 2rem #0c2ff0, 0 0.5rem 3rem #0c2ff0;
    }
  }
  
  @keyframes flicker {
    from {
      opacity: 1;
    }
  
    4% {
      opacity: 0.9;
    }
  
    6% {
      opacity: 0.85;
    }
  
    8% {
      opacity: 0.95;
    }
  
    10% {
      opacity: 0.9;
    }
  
    11% {
      opacity: 0.922;
    }
  
    12% {
      opacity: 0.9;
    }
  
    14% {
      opacity: 0.95;
    }
  
    16% {
      opacity: 0.98;
    }
  
    17% {
      opacity: 0.9;
    }
  
    19% {
      opacity: 0.93;
    }
  
    20% {
      opacity: 0.99;
    }
  
    24% {
      opacity: 1;
    }
  
    26% {
      opacity: 0.94;
    }
  
    28% {
      opacity: 0.98;
    }
  
    37% {
      opacity: 0.93;
    }
  
    38% {
      opacity: 0.5;
    }
  
    39% {
      opacity: 0.96;
    }
  
    42% {
      opacity: 1;
    }
  
    44% {
      opacity: 0.97;
    }
  
    46% {
      opacity: 0.94;
    }
  
    56% {
      opacity: 0.9;
    }
  
    58% {
      opacity: 0.9;
    }
  
    60% {
      opacity: 0.99;
    }
  
    68% {
      opacity: 1;
    }
  
    70% {
      opacity: 0.9;
    }
  
    72% {
      opacity: 0.95;
    }
  
    93% {
